import axios from 'axios';

let JLApiUtils = {};
//let base = "http://39.107.78.95:8080/jlmanager";//调用数据的域地址
let base = "http://localhost:8885/#";
axios.defaults.timeout = 20000;//2秒超时

JLApiUtils.upload = 'http://39.107.78.95:8080/jlmanager/comm/upload/image';
//JLApiUtils.upload = "http://localhost:8080/api/comm/upload/image";
// 获取收模列表
JLApiUtils.addProducts = params => {
    return axios.post(`http://localhost:8885/#/offer/per/add/product`, params).then(res => res.data);
};


export default JLApiUtils;



